<template>
  <div class="homePage">
    <swiper :options="swiperOptionTop" ref="mySwiperTop" class="topBox">
      <swiper-slide v-for="item in items" :class="[item.className]">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination">
      </div>
      <div class="swiper-button-prev" slot="button-prev">
      </div>
      <div class="swiper-button-next" slot="button-next">
      </div>
    </swiper>
    <swiper :options="swiperOptionBottom" ref="mySwiperBottom" class="bottomBox">
      <swiper-slide v-for="item in items" :class="[item.className]"></swiper-slide>
    </swiper>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "homePage",
  data() {
    return {
      swiperOptionTop: {
        spaceBetween: 10,
        loop: true,
        loopedSlides: 5,
        grabCursor: true,
        effect: "fade",
        speed: 2000,
        parallax: true,
        // autoplay: {
        //   delay: 5000 //5秒切换一次
        // },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        pagination: {
          el: ".swiper-pagination",
          type: "bullets",
          clickable: true
        },
        on: {
          slideChangeTransitionEnd() {
            // this.params.effect = "cube";
          }
        }
      },
      swiperOptionBottom: {
        spaceBetween: 10,
        slidesPerView: 4,
        touchRatio: 0.2,
        loop: true,
        grabCursor: true,
        loopedSlides: 5, //looped slides should be the same
        slideToClickedSlide: true
      },
      items: [
        {
          className: "bg01",
          header: "疏雨闲处",
          content:
            "纵我不甘于改写,缘我选择了我的路。我当无悔于初心,更要无愧于未,无端晚秋未晚。"
        },
        { className: "bg02", header: "过去", content: "将来" },
        { className: "bg03", header: "111", content: "222" },
        { className: "bg04", header: "333", content: "444" },
        { className: "bg05", header: "555", content: "666" }
      ]
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    // mySwiperTop() {
    //   return this.$refs.mySwiperTop.swiper;
    // },
    // mySwiperBottom() {
    //   return this.$refs.mySwiperBottom.swiper;
    // }
  },
  mounted() {
    const swiperTop = this.$refs.mySwiperTop.swiper;
    const swiperBottom = this.$refs.mySwiperBottom.swiper;
    swiperTop.controller.control = swiperBottom;
    swiperBottom.controller.control = swiperTop;
  },
  methods:{
    enterIn(){


    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.enterBtn{
  padding:5px 10px;
  background:lightseagreen;
  border-radius: 5px;
  top:50%;
  position: absolute;
  color:#fafafa;
  border:1px solid darkcyan;
}
.enterBtn:hover{
  background:lightsalmon;
  border-color:darkgoldenrod
}
.homePage {
  height: 100%;
  background-color: #888;
  .topBox {
    height: 68%;
  }
  .bottomBox {
    margin-top: 2%;
    height: 28%;
    opacity: 0.3;
  }
}
.swiper-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
.swiper-slide {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 0;
}
.gallery-top {
  height: 80%;
  width: 100%;
}
.gallery-thumbs {
  height: 20%;
  box-sizing: border-box;
  padding: 10px 0;
}
.gallery-thumbs .swiper-slide {
  height: 100%;
  opacity: 0.5;
}
.gallery-thumbs .swiper-slide-active {
  opacity: 1;
}
.bg01 {
  background-image: url("/static/imgs/bg01.jpg");
}
.bg02 {
  background-image: url("/static/imgs/bg02.jpg");
}
.bg03 {
  background-image: url("/static/imgs/bg03.jpg");
}
.bg04 {
  background-image: url("/static/imgs/bg04.jpg");
}
.bg05 {
  background-image: url("/static/imgs/bg05.jpg");
}
</style>



